@import url(https://fonts.googleapis.com/css2?family=Rubik:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Noto+Serif+JP:wght@200&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lobster&display=swap);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.MuiDrawer-paper {
  width: 35px !important;
  background: linear-gradient(rgb(255, 199, 96), rgb(255, 150, 167));
}

.icon{
-webkit-transform: translate(4px,69px) !important;
        transform: translate(4px,69px) !important;
color: seagreen !important;
}

@media only screen and (min-width: 700px) {
  .MuiDrawer-paper {
    width: 45px !important;
  }
}

.navbar {
  margin-left: 37px;
}

.MuiToolbar-root {
  -webkit-justify-content: flex-end !important;
          justify-content: flex-end !important;
}

.user {
  padding: 5px;
  color: black;
  border-radius: 10px;
  font-family: 'Rubik', sans-serif;
}

.user:hover {
  cursor: pointer;
  background-color: cornsilk;
}

.MuiAppBar-colorPrimary {
  background-color: white !important;
}

.userlist {
  z-index: 3000;
  color: black;
  position: absolute;
  background-color: lightblue;
  top: 39px;
  right: 0;
  width: 335px;
  height: auto;
  border-radius: 10px;
}

.userlist > ul {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.userlist > ul > li {
  list-style: none;
  margin: 5px;
  cursor: pointer;
}

.contactadd {
  margin-left: 51px;
}

.contacthead {
  display: -webkit-flex;
  display: flex;
  margin: 10px;
  -webkit-align-items: center;
          align-items: center;
}

.search {
  display: -webkit-flex;
  display: flex;
}

.search > input {
  border-radius: 10px;
  padding: 10px;
  margin-right: 5px;
  outline: none;
  background-color: rgb(245, 245, 245);
}

button {
  background: linear-gradient(rgb(255, 199, 96), rgb(255, 150, 167));
  padding: 5px;
  border-radius: 5px;
  cursor: pointer;
  outline: none;
}

@media only screen and (min-width: 700px) {
  .contactadd {
    margin-left: 180px;
    margin-top: 20px;
  }

  .search > input {
    width: 270px;
  }

  button {
    padding: 10px;
    width: 120px;
  }
}

.contaclist {
  margin-left: 39px;
  width: 84vw;
  max-width: 400px;
}

.infohead {
  display: -webkit-flex;
  display: flex;
  margin: 5px;
  width: 100%;
  height: 40px;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  margin-top: 15px;
  padding: 10px;
  border-radius: 10px;
  background-color: rgb(245, 245, 245);
}

.displaycontact {
  display: grid;
  grid-template-columns: 0fr 2fr 1fr;
  -webkit-align-items: center;
          align-items: center;
  padding: 5px;
}

.displaycontact:hover {
  background: gainsboro;
  cursor: pointer;
  border-radius: 10px;
}

.basicinfo {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: baseline;
          align-items: baseline;
  margin-left: 5px;
  margin-bottom: 10px;
  margin-top: 10px;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.MuiAvatar-root {
  width: 30px !important;
  height: 30px !important;
}

.chat {
  -webkit-transform: translateY(10px) !important;
          transform: translateY(10px) !important;
}

.backbtn{
  width: 50px;
  margin-bottom: 5px;
}

.diplayposition {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  width: 84vw;
}

.listcontact{
  background-color: rgb(209, 209, 51) !important;
}

@media only screen and (min-width: 700px) {
  .basicinfo {
    -webkit-flex: 1 1;
            flex: 1 1;
  }
  .diplayposition {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }

  .contaclist {
    margin-left: 180px;
    margin-top: 50px;
  }

  .displaycontact {
    grid-template-columns: 0.4fr 1fr 1.5fr;
  }

  .backbtn{
    width: 56px;
    height: 35px;
    -webkit-transform: translate(-16px,-117px);
            transform: translate(-16px,-117px);
  }
}

.contactinfo {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  background-color: gainsboro;
  padding: 7px;
  margin-top: 5px;
  border-radius: 10px;
  width: 100%;
}

.userinfo {
  display: grid;
  grid-template-columns: 35% 10%;
}

.avatar{
  background-color: rgb(209, 209, 51) !important;
  width: 50px !important;
  height: 50px !important;
}

.left{
  color: gray;
}

.info_head{
   display: -webkit-flex;
   display: flex;
   -webkit-flex-direction: column;
           flex-direction: column;
   -webkit-align-items: center;
           align-items: center;
}

.infodetail > p {
  margin: 22px;
  font-family: 'Noto Serif JP', serif;
}

@media only screen and (min-width: 700px) {
  .contactinfo {
    -webkit-transform: translate(-140px,-140px);
            transform: translate(-140px,-140px);
    width: 407px;
  }
}

.userchat {
  background-color: lightgrey;
  width: 100%;
  height: 50vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  border-radius: 10px;
  position: relative;
}

.userchat > input {
  padding: 12px;
  border-radius: 10px;
  margin-bottom: 1px;
}

.userchat > input:active {
  outline: none;
}

.chatuser {
  width: 100%;
  height: 46px;
  -webkit-flex: 1 1;
          flex: 1 1;
  margin-top: 7px;
  overflow: scroll;
}

.sendbtn {
  padding: 10px;
}

.chathead{
  background-color: skyblue;
  padding: 10px;
  border-radius: 10px;
  font-family: 'Lobster', cursive;
  color: forestgreen;
  letter-spacing: 3px;
}

.message {
  display: block;
  position: relative;
}

span{
  position: absolute;
  left: 21px;
  top: -15px;
}

.message p{
  margin: 11px;
  padding: 8px;
  background-color: skyblue;
  border-radius: 10px;
  width: 80px;
}

@media only screen and (min-width: 700px) {
  .userchat {
    width: 32%;
    -webkit-transform: translate(-140px,-140px);
            transform: translate(-140px,-140px);
    height: 500px;
  }

  .sendbtn {
    width: 100%;
  }
}

